import { React } from "react";
import { saveAs } from "file-saver";

const Button = (props) => {
  const handleClick = () => {
    e;
    const name = `${props.name}.jpg`;
    saveAs(props.url, name);
  };
  return (
    <>
      <div className="btn">
        <button onClick={handleClick}>Download</button>
      </div>
    </>
  );
};

export default Button;
