import React, { useState } from "react";
import Button from "./Button";
import Search from "./Search";

const intro = "Free to use images!";
const Image = () => {
  const [name, setName] = useState("");
  let url = `https://source.unsplash.com/random/380x300/?${name}`;
  return (
    <>
      <Search imageSet={setName} />
      <div className="image">
        {name ? (
          <img src={url} alt="searched" />
        ) : (
          <div className="intro">{intro}</div>
        )}
      </div>
      <Button url={url} name={name} />
    </>
  );
};
export default Image;
